<template>
  <div class="ChooseCampus">
    <div class="input-div">
      <span></span>
      <el-select v-model="campus" placeholder="" @change="campus_select">
        <el-option v-for="item in campus_options" :key="item" :value="item"></el-option>
      </el-select>
    </div>

    <div class="input-div">
      <span></span>
      <el-select v-model="detail" placeholder="">
        <el-option v-for="item in detail_options" :key="item" :value="item"></el-option>
      </el-select>
    </div>
  </div>
</template>

<script>
export default {
  name: "ChooseCampus",
  data: function() {
    return {
      campus: "",
      detail: "",
      campus_options: [
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      detail_options: [""],
      software_options: ["", "", ""],
      biology_options: ["", "", ""],
      physics_options: ["", "", "", ""],
      environment_options: [""],
      medicine_options: [""]
    };
  },
  methods: {
    campus_select: function(e) {
      this.$data.detail = "";
      switch (e) {
        case "": {
          this.$data.detail_options = this.$data.software_options;
          break;
        }
        case "": {
          this.$data.detail_options = this.$data.physics_options;
          break;
        }
        case "": {
          this.$data.detail_options = this.$data.environment_options;
          break;
        }
        case "": {
          this.$data.detail_options = this.$data.medicine_options;
          break;
        }
        case "": {
          this.$data.detail_options = this.$data.biology_options;
          break;
        }
        default: {
          this.$data.detail_options = [""];
        }
      }
    }
  }
};
</script>

<style scoped>
span {
  display: inline-block;
  width: 3em;
  margin-right: 1em;
}

.ChooseCampus {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.input-div {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding-right: 5%;
}
</style>
