<template>
  <div class="choose-room">
    <div class="input-div">
      <span></span>
      <el-select v-model="build" placeholder="" @change="build_select">
        <el-option v-for="item in build_options" :key="item" :label="item" :value="item"></el-option>
      </el-select>
    </div>

    <div v-if="has_singleRoom" class="input-div">
      <span></span>
      <el-select v-model="door" placeholder="">
        <el-option v-for="item in door_options" :key="item" :label="item" :value="item"></el-option>
      </el-select>
    </div>

    <div v-if="has_singleRoom" class="input-div">
      <span></span>
      <el-select v-model="singleRoom" placeholder="">
        <el-option v-for="item in singleRoom_options" :key="item" :label="item" :value="item"></el-option>
      </el-select>
    </div>

    <div class="input-div">
      <span></span>
      <el-input v-model="room" placeholder=""></el-input>
    </div>
  </div>
</template>

<script>
export default {
  name: "ChooseRoom",
  data: function() {
    return {
      build: "1",
      door: "",
      singleRoom: "",
      room: "101",
      build_options: [
        "1",
        "2",
        "3",
        "4",
        "5",
        "6",
        "7"
      ],
      door_options: ["1", "2", "3", "4", "5"],
      singleRoom_options: ["1", "2", "3", "4"],
      has_singleRoom: false
    };
  },
  methods: {
    build_select: function() {
      switch (this.$data.build) {
        case "1": {
          this.$data.has_singleRoom = false;
          this.$data.door = "";
          this.$data.singleRoom = "";
          break;
        }
        case "2": {
          this.$data.has_singleRoom = false;
          this.$data.door = "";
          this.$data.singleRoom = "";
          break;
        }
        case "3": {
          this.$data.has_singleRoom = false;
          this.$data.door = "";
          this.$data.singleRoom = "";
          break;
        }
        case "4": {
          this.$data.has_singleRoom = true;
          this.$data.door = "1";
          this.$data.singleRoom = "1";
          break;
        }
        case "5": {
          this.$data.has_singleRoom = true;
          this.$data.door = "1";
          this.$data.singleRoom = "1";
          break;
        }
        case "6": {
          this.$data.has_singleRoom = true;
          this.$data.door = "1";
          this.$data.singleRoom = "1";
          break;
        }
        case "7": {
          this.$data.has_singleRoom = true;
          this.$data.door = "1";
          this.$data.singleRoom = "1";
          break;
        }
        default: {
          this.$data.has_singleRoom = false;
          this.$data.door = "";
          this.$data.singleRoom = "";
          break;
        }
      }
    },
    reset: function() {
      this.$data.has_singleRoom = false;
      this.$data.build = "1";
      this.$data.door = "";
      this.$data.singleRoom = "";
      this.$data.room = "101";
    }
  }
};
</script>


<style scoped>
.choose-room {
  display: inline-block;
  width: 85%;
  margin: auto;
}

.input-div:last-child {
  margin-right: 5vw;
  padding: 0;
}

.choose-room:nth-child(1) {
  padding-left: 5vw;
}

.input-div {
  display: inline-block;
  padding-right: 5%;
  margin-top: 3vh;
}

span {
  display: inline-block;
  width: 3em;
  margin-right: 1em;
}

.el-input {
  width: 50%;
}
</style>
